.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  object-fit: contain;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  width: 100%;
  min-width: 320px;
  max-width: 100%;
  overflow-x: hidden;
  height: 200vh;
}

body {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  margin: 0;
  font-family: "Roboto", "Arial", san-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
  background-image: linear-gradient(99.89deg, #3625AF 0%, #C46BD5 98.82%);
}

.button {
  padding: 0 46px;
  font-family: inherit;
  font-size: 16px;
  line-height: 60px;
  background-color: #302594;
  border: none;
  border-radius: 4px;
  box-shadow: 0px 22px 22px rgba(0, 0, 0, 0.15);
  color: #ffffff;
  text-transform: uppercase;
  cursor: pointer;
}
.button:hover, .button:focus {
  outline: none;
  background-color: rgba(48, 37, 148, 0.6);
}
.button:active {
  background-color: rgba(48, 37, 148, 0.3);
}
.button:disabled {
  background-color: #bdbdbd;
}
@media (max-width: 1280px) {
  .button {
    line-height: 46px;
  }
}
@media (max-width: 768px) {
  .button {
    line-height: 36px;
  }
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  src: url("../fonts/Roboto-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2");
  src: url("../fonts/Roboto-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  src: url("../fonts/Roboto-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Roboto-Black.woff2") format("woff2");
  src: url("../fonts/Roboto-Black.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2");
  src: url("../fonts/Inter-SemiBold.woff") format("woff");
  font-display: swap;
}
.header__container {
  position: relative;
  max-width: 1635px;
  padding: 0 15px;
  margin: 30px auto 54px;
  display: flex;
  align-items: start;
}
@media (max-width: 1280px) {
  .header__container {
    padding: 0 30px;
  }
}
@media (max-width: 768px) {
  .header__container {
    padding: 0 15px;
  }
}

.header__logo {
  margin-left: 97px;
  margin-right: 105px;
}
@media (max-width: 1280px) {
  .header__logo {
    margin-left: 0;
    margin-right: auto;
  }
}
@media (max-width: 768px) {
  .header__logo {
    max-width: 60px;
  }
}

.header__navigation-button {
  position: relative;
  width: 24px;
  height: 2px;
  background-color: #ffffff;
  border: none;
  display: none;
}
.header__navigation-button::before, .header__navigation-button::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  left: 0;
  background-color: #ffffff;
}
.header__navigation-button::before {
  bottom: -5px;
}
.header__navigation-button::after {
  top: -5px;
}
@media (max-width: 1280px) {
  .header__navigation-button {
    display: block;
    margin-left: 20px;
  }
}

.header__button {
  margin-left: auto;
  min-width: 246px;
}
@media (max-width: 1280px) {
  .header__button {
    display: none;
  }
}
@media (max-width: 768px) {
  .header__button {
    display: none;
  }
}

@media (max-width: 1280px) {
  .nav {
    order: 3;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 1280px) {
  .nav__list {
    display: none;
  }
}

.nav__item {
  margin-right: 39px;
  margin-bottom: 10px;
  font-family: "inter", "Arial", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
}
@media (max-width: 1280px) {
  .nav__item {
    font-size: 14px;
    line-height: 16px;
    margin-right: 0;
  }
}

.nav__item:first-child {
  position: relative;
  padding-left: 28px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-decoration: none;
  color: inherit;
  position: relative;
  padding-bottom: 6px;
}
.nav__link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  transform: scale(0);
  transition: all 0.3s ease;
  background-color: #ffffff;
}
.nav__link:hover::after, .nav__link:focus::after {
  transform: scale(1);
}
@media (max-width: 1280px) {
  .nav__link {
    color: #302594;
  }
}

.nav__button {
  position: absolute;
  top: -3px;
  left: 0;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #ffffff;
  border: none;
}
.nav__button::after, .nav__button::before {
  position: absolute;
  content: "";
  width: 17px;
  height: 2px;
  background-color: #302594;
  top: 40%;
  left: 10%;
}
.nav__button::after {
  transform: rotate(45deg);
}
.nav__button::before {
  transform: rotate(-45deg);
}

.nav__dropdown-menu {
  position: absolute;
  top: 32px;
  left: -5px;
  min-width: 154px;
  margin: 0;
  padding: 6px 19px 7px;
  list-style-type: none;
  background-color: #ffffff;
  border-radius: 7px;
}
@media (max-width: 1280px) {
  .nav__dropdown-menu {
    z-index: 3;
    width: 100%;
    min-height: 100vh;
    top: 24px;
  }
}

.nav__dropdown-item {
  margin-bottom: 12px;
}

.nav__dropdown-link {
  font-family: "inter", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  font-weight: 600;
  color: #5C39BA;
  text-decoration: none;
  text-transform: uppercase;
}

.nav__dropdown-item:last-child {
  margin-bottom: 0;
}

.nav__button--close,
.nav__dropdown-menu--close {
  display: none;
}

@media (max-width: 1280px) {
  .nav__menu--opened .nav__list {
    position: absolute;
    display: block;
    top: 14px;
    right: 0;
    width: 70%;
    min-height: 100vh;
    z-index: 2;
    margin-left: 35px;
    padding: 25px 25px;
    background-color: #ffffff;
  }
}

@media (max-width: 1280px) {
  .nav__menu--opened .header__navigation-button {
    position: absolute;
    top: 6px;
    right: 12px;
    background-color: transparent;
  }
  .nav__menu--opened .header__navigation-button::after {
    width: 18px;
    transform: rotate(45deg);
    background-color: #ffffff;
  }
  .nav__menu--opened .header__navigation-button::before {
    width: 18px;
    top: -5px;
    transform: rotate(135deg);
    background-color: #ffffff;
  }
}

@media (max-width: 1280px) {
  .nav__menu--closed .nav__list {
    display: none;
  }
}

.main {
  position: relative;
}

.main__bg-image {
  position: absolute;
  top: 0;
  right: -100%;
  z-index: -1;
  border-radius: 150px 0px 0px 0px;
  transition: all 0.8s ease 0.2s;
  opacity: 0;
}
@media (max-width: 1280px) {
  .main__bg-image {
    display: none;
  }
}

.main__bg-image.active {
  right: 0;
  opacity: 1;
}

.main__bg-image.out {
  right: -100%;
  opacity: 0;
}

.main__container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (max-width: 1280px) {
  .main__container {
    padding: 0 30px;
  }
}
@media (max-width: 768px) {
  .main__container {
    padding: 0 15px;
  }
}

.main__group-wrapper {
  display: grid;
  grid-template-columns: 1fr 508px;
  margin-bottom: 40px;
}
@media (max-width: 1280px) {
  .main__group-wrapper {
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
  }
}
@media (max-width: 768px) {
  .main__group-wrapper {
    grid-template-columns: 1fr;
  }
}

.main__content-wrapper {
  max-width: 622px;
}

.main__title {
  margin: 0;
  margin-top: 44px;
  margin-bottom: 40px;
  padding: 0;
  font-family: "Roboto", "Arial", san-serif;
  font-size: 55px;
  line-height: 56px;
  font-weight: 900;
  transform: translateX(-120%);
  opacity: 0;
  transition: all 0.8s ease 0.2s;
}
@media (max-width: 1280px) {
  .main__title {
    font-size: 32px;
    line-height: 36px;
  }
}
@media (max-width: 768px) {
  .main__title {
    font-size: 28px;
    margin: 30px 0;
  }
}

.main__title sup {
  font-size: 33px;
}
@media (max-width: 1280px) {
  .main__title sup {
    font-size: 18px;
    line-height: 22px;
  }
}

.main__text {
  position: relative;
  margin: 0;
  margin-bottom: 64px;
  padding: 0;
  padding-bottom: 10px;
  max-width: 345px;
  font-size: 24px;
  line-height: 28px;
  font-weight: 400;
  transform: translateX(-120%);
  opacity: 0;
  transition: all 0.8s ease 0.4s;
}
.main__text::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  height: 5px;
  width: 50%;
  background-color: #BAF1FF;
}
@media (max-width: 768px) {
  .main__text {
    font-size: 20px;
  }
}

.main__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 220px 1fr;
  column-gap: 45px;
  row-gap: 30px;
  transform: translateX(-120%);
  opacity: 0;
  transition: all 0.8s ease 0.6s;
}
@media (max-width: 1280px) {
  .main__list {
    display: flex;
    flex-wrap: wrap;
  }
}

.main .active {
  transform: translateX(0px);
  opacity: 1;
}

.main__item {
  position: relative;
  padding-left: 60px;
}
.main__item::before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.main__item--van::before {
  width: 45px;
  height: 36px;
  left: 0;
  background-image: url(../images/van.svg);
}

.main__item--guarantee::before {
  width: 30px;
  height: 45px;
  background-image: url(../images/guarantee.svg);
}

.main__item--time::before {
  width: 40px;
  height: 38px;
  background-image: url(../images/time-management.svg);
}

.main__scroll-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main__scroll-image {
  margin-bottom: 14px;
  animation: down 1.5s infinite;
  cursor: pointer;
}

@keyframes down {
  0% {
    transform: translate(0);
  }
  50% {
    transform: translateY(30px);
  }
  100% {
    transform: translate(0);
  }
}
.main__scroll-wrapper span {
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
}

.filters__wrapper {
  padding: 30px;
  margin-top: 40px;
  background-color: #ffffff;
  color: #000000;
  border-radius: 10px;
  transform: translateX(120%);
  opacity: 0;
  transition: all 0.8s ease 0.2s;
}

.filters__wrapper .active {
  transform: translateX(0px);
  opacity: 1;
}

.filters__text {
  margin: 0;
  margin-bottom: 24px;
  padding: 0;
  max-width: 270px;
  font-size: 25px;
  line-height: 30px;
  font-weight: 600;
}
@media (max-width: 768px) {
  .filters__text {
    font-size: 20px;
  }
}

.filters__square {
  margin-bottom: 32px;
}

.filters__square-title,
.filters__type-title,
.filters__rooms-title {
  font-size: 12px;
  line-height: 13px;
  text-transform: uppercase;
}

.filters__square-title,
.filters__rooms-title {
  margin-bottom: 16px;
}

.filters__type-title {
  margin-bottom: 24px;
}

.filters__square-title {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.filters__square-number {
  margin-top: -7px;
  font-family: Arial, sans-serif;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: lowercase;
}

.filters__square-number sup {
  font-size: 10px;
  line-height: 10px;
}

.filters__range-scale {
  position: relative;
}

.filters__range-bar {
  height: 12px;
  width: 100%;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(to right, #BE69D5 0%, #BE69D5 45%, #BAF1FF 45%, #BAF1FF 100%);
  background-color: #BE69D5;
}

.filters__range-toggle {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-100%, -20%);
  width: 33px;
  height: 33px;
  border: none;
  border-radius: 50%;
  background-color: #302594;
  cursor: pointer;
}

.filters__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}

.filters__type {
  margin-bottom: 30px;
}

.filters__rooms {
  margin-bottom: 22px;
}

.filters__button {
  display: block;
  margin: 0 auto;
}

.controls {
  margin-right: 35px;
}

.filters__type .controls:nth-child(2n) {
  margin-right: 20px;
}

.filters__rooms .controls {
  margin-right: 64px;
}

.filters__rooms .controls:nth-child(3n) {
  margin-right: 56px;
}

.controls:last-child {
  margin-right: 0;
}

.controls__input {
  margin: 0;
  padding: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.controls__label {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 16px;
  font-weight: 500;
  cursor: pointer;
}
@media (max-width: 1280px) {
  .controls__label {
    margin-bottom: 6px;
  }
}

.controls__input:focus + .controls__mark::after {
  outline: rgb(59, 153, 252) auto 5px;
}

.controls__mark {
  margin-right: 10px;
  position: relative;
}
.controls__mark::after {
  position: absolute;
  content: "";
  top: 5px;
  left: 5px;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background-color: #BE69D5;
  display: none;
}

.controls__input:hover ~ .controls__mark::after,
.controls__input:checked ~ .controls__mark::after {
  display: block;
}